<template>
  <button v-if="showScrollToTop" @click="scrollToTop" class="scrollToTopButton">
    <img src="@/assets/images/icon_up.png" alt="맨위로" />
  </button>
</template>

<script lang="ts">
import { defineComponent, ref, onUnmounted } from 'vue';

export default defineComponent({
  name: "ScrollTopButton",
  setup() {
    const showScrollToTop = ref(false);
    const checkScroll = () => showScrollToTop.value = window.scrollY > 100;
    window.addEventListener('scroll', checkScroll);

    const scrollToTop = () => window.scrollTo({ top: 0, behavior: 'smooth' });
    
    onUnmounted(() => {
      window.removeEventListener('scroll', checkScroll);
    });

    return { showScrollToTop, scrollToTop };
  }
});
</script>